import React from 'react';
import { graphql, Link, PageProps } from 'gatsby';

import Layout from '../../components/layout';
import SEO from '../../components/seo';

type Props = PageProps<GatsbyTypes.NewsArticlesQuery>;

const NewsIndex: React.FC<Props> = ({ data }) => {
  const { nodes: posts } = data.allMarkdownRemark;

  return (
    <Layout>
      <SEO title="News" />

      <div className="pt-16 pb-20 px-4 sm:px-6 lg:pt-24 lg:pb-28 lg:px-8">
        <div className="relative max-w-lg mx-auto lg:max-w-7xl">
          <h2 className="text-3xl leading-9 tracking-tight font-extrabold text-gray-900 sm:text-4xl sm:leading-10">
            News
          </h2>

          <div className="mt-6 grid gap-16 border-t-2 border-gray-100 pt-10 lg:grid-cols-2 lg:col-gap-5 lg:row-gap-12">
            {posts.map((node) => {
              const title = node.frontmatter?.title || node.fields?.slug;

              return (
                <article key={node.id}>
                  <header>
                    {node.frontmatter?.date && (
                      <p className="text-sm leading-5 text-gray-500">
                        {/* TODO: Add `datetime="2020-03-16` to `<time>` */}
                        <time>{node.frontmatter.date}</time>
                      </p>
                    )}
                    <Link className="block" to={node.fields?.slug || ''}>
                      <h3 className="mt-2 text-xl leading-7 font-semibold text-gray-900">
                        {title}
                      </h3>
                    </Link>
                  </header>
                  <section>
                    {/* eslint-disable react/no-danger */}
                    <p
                      className="mt-3 text-base leading-6 text-gray-500"
                      dangerouslySetInnerHTML={{
                        __html: node.frontmatter?.description || node.excerpt || '',
                      }}
                    />
                    {/* eslint-enable react/no-danger */}
                  </section>
                  <div className="mt-3">
                    <Link
                      className="text-base leading-6 font-semibold text-indigo-600 hover:text-indigo-500 transition ease-in-out duration-150"
                      to={node.fields?.slug || ''}
                    >
                      Read full story
                    </Link>
                  </div>
                </article>
              );
            })}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default NewsIndex;

export const pageQuery = graphql`
  query NewsArticles {
    allMarkdownRemark(
      filter: { fields: { slug: { glob: "/news/*" } } }
      sort: { order: DESC, fields: [frontmatter___date] }
    ) {
      nodes {
        excerpt(pruneLength: 250)
        id
        fields {
          slug
        }
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          description
        }
      }
    }
  }
`;
